import React from 'react';
import '../styles/projectDetails.css';

interface detailsProps {
	close: any;
	project: {
		name: string;
		summary: string;
		bgColor: string;
		textColor: string;
	};
}

const projectDetails = (props: detailsProps) => {
	return (
		<div className='projectDetails'>
			<div className='projectDetails-content'>
				<button
					className='close-btn'
					onClick={() => {
						console.log('projects closed');
						props.close(false);
					}}>
					close
				</button>

				<p> {props.project.name} </p>
				<p> {props.project.summary} </p>
			</div>
		</div>
	);
};

export default projectDetails;
