import React, { useState, useEffect, useRef } from 'react';
import '../styles/projects.css';
import allProjects from '../allProjects.json';
import ProjectDetails from './projectDetails';

const Projects = () => {
	const [id, changeId] = useState(0);
	const [moreOpened, openMore] = useState(false);
	const projectContainer: React.MutableRefObject<any> = useRef();

	interface dataType {
		[key: number]: {
			id: number;
			name: string;
			summary: string;
			image: string;
			textColor: string;
			bgColor: string;
		};
	}

	const data: dataType = allProjects;
	const currentProject = data[id];
	const style = {
		background: `${currentProject.bgColor}`,
		color: `${currentProject.textColor}`,
	};

	useEffect(() => {
		console.log('projects loaded firs time.');
		projectContainer.current.classList.add('show');
	}, []);

	return (
		<div className='projects'>
			<div className='projects-content' style={style} ref={projectContainer}>
				<div className='projects-description'>
					<div>
						<h3>{currentProject.name}</h3>
						<p>{currentProject.summary}</p>
						<button
							onClick={() => openMore(true)}
							style={{ color: `${currentProject.textColor}` }}>
							More Info.
						</button>
					</div>
				</div>
				<div className='projects-image'>
					<img alt='screenshot' src={currentProject.image} />
				</div>
			</div>

			<div className='projects-nav'>
				<button onClick={() => changeId(0)}> {data[0].name} </button>
				<button onClick={() => changeId(1)}> {data[1].name} </button>
				<button onClick={() => changeId(2)}> {data[2].name} </button>
			</div>

			{moreOpened ? (
				<ProjectDetails close={openMore} project={currentProject} />
			) : (
				''
			)}
		</div>
	);
};

export default Projects;
